
import { computed, defineComponent } from 'vue';

import Card from '../Card.vue';
import ArticleInfo from '../ArticleInfo.vue';

export default defineComponent({
  props: {
    blog: null,
  },
  components: {
    Card,
    ArticleInfo,
  },
  setup(props) {
    const src = computed(
      () => process.env.VUE_APP_IMAGE_LOAD_BASE + props.blog.cover,
    );
    return {
      src,
    };
  },
});
